.top-menu {
  display: flex;
  height: 80px;
  border-bottom: 1px solid #272727;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
}
.top-menu .user-menu {
  display: flex;
  font-size: 14px;
  align-items: center;
  position: relative;
  gap: 5px;
}
.top-menu .user-menu .login-button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #272727;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 8px;
  padding-bottom: 8px;
  border-radius: 20px;
  gap: 5px;
  font-size: 13px;
  transition: all 0.2s ease;
}
.top-menu .user-menu .login-button svg {
  width: 20px;
}
.top-menu .user-menu .login-button svg path {
  transition: all 0.2s ease;
}
.top-menu .user-menu .login-button:hover {
  color: #e9ff70;
  background-color: rgba(233, 255, 112, 0.05);
}
.top-menu .user-menu .login-button:hover svg path {
  fill: #e9ff70;
}
.top-menu .user-menu .user-avatar {
  width: 35px;
  height: 35px;
  border-radius: 20px;
  background-color: #272727;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 22px;
  color: #e3e3e3;
  cursor: pointer;
  user-select: none;
  transition: all 0.2s ease;
}
.top-menu .user-menu .user-avatar:hover {
  color: #e9ff70;
  background-color: rgba(233, 255, 112, 0.05);
}
.top-menu .user-menu .user-avatar .profile-image {
  border-radius: 25px;
}
.top-menu .user-menu .user-sub-menu {
  opacity: 0;
  pointer-events: none;
  position: absolute;
  right: 0;
  bottom: -180px;
  width: 250px;
}
.top-menu .user-menu .user-sub-menu.active {
  opacity: 1;
  pointer-events: all;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content {
  background-color: #171717;
  border: 1px solid #272727;
  padding: 10px;
  border-radius: 6px;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content .user-status {
  font-size: 10px;
  text-transform: uppercase;
  border: 1px solid #272727;
  padding: 7px 10px 5px 10px;
  border-radius: 4px;
  display: inline-block;
  margin-bottom: 10px;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content .user-status.premium {
  color: #e9ff70;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content .email {
  font-size: 13px;
  color: #989b99;
  margin-top: 5px;
}
@media screen and (max-width: 762px) {
  .top-menu .user-menu .user-sub-menu .user-sub-menu-content .email {
    font-size: 20px;
  }
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content .user-menu-list {
  border-top: 1px solid #272727;
  margin-top: 10px;
  padding-top: 10px;
}
.top-menu .user-menu .user-sub-menu .user-sub-menu-content .user-menu-list li {
  padding-top: 5px;
  font-size: 13px;
}
@media screen and (max-width: 762px) {
