.accordion-menu {
  width: 100%;
  max-width: 300px;
  padding: 10px;
}
.accordion-menu .accordion-item {
  margin-bottom: 8px;
  background: #171717;
  border-radius: 8px;
  border: 1px solid #272727;
  overflow: hidden;
}
.accordion-menu .accordion-item .accordion-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  cursor: pointer;
  transition: all 0.2s ease;
}
.accordion-menu .accordion-item .accordion-header h3 {
  margin: 0;
  font-size: 14px;
  font-weight: 500;
  color: #e3e3e3;
}
.accordion-menu .accordion-item .accordion-header .icon {
  font-size: 18px;
  color: #989b99;
  transition: transform 0.3s ease;
}
.accordion-menu .accordion-item .accordion-header:hover {
  background-color: rgba(233, 255, 112, 0.05);
}
.accordion-menu .accordion-item .accordion-header:hover h3 {
  color: #e9ff70;
}
.accordion-menu .accordion-item .accordion-header:hover .icon {
  color: #e9ff70;
}
.accordion-menu .accordion-item .accordion-content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-out;
}
.accordion-menu .accordion-item .accordion-content ul {
  list-style: none;
  padding: 8px 0;
  margin: 0;
}
.accordion-menu .accordion-item .accordion-content ul li a {
  display: block;
  padding: 12px 20px 12px 35px;
  color: #989b99;
  text-decoration: none;
  font-size: 13px;
  transition: all 0.2s ease;
}
.accordion-menu .accordion-item .accordion-content ul li a:hover {
  background-color: rgba(233, 255, 112, 0.05);
  color: #e9ff70;
  padding-left: 40px;
}
.accordion-menu .accordion-item.active .accordion-header h3 {
  color: #e9ff70;
}
.accordion-menu .accordion-item.active .accordion-header .icon {
  color: #e9ff70;
  transform: rotate(45deg);
}
.accordion-menu .accordion-item.active .accordion-content {
  max-height: 300px;
  border-top: 1px solid #272727;
}
.accordion-menu .accordion-item.active {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.card {
  background-color: #171717;
  border: 1px solid #272727;
  border-radius: 6px;
  overflow: hidden;
  flex: 1;
}
.card .card-image {
  width: 100%;
  height: 180px;
  overflow: hidden;
}
.card .card-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card .card-content {
  padding: 20px;
}
.card .card-content .card-title {
  font-size: 18px;
  font-weight: 600;
  color: #e3e3e3;
  margin-bottom: 10px;
}
.card .card-content .card-text {
  font-size: 14px;
  color: #989b99;
  margin-bottom: 20px;
  line-height: 1.5;
}
.card .card-content .card-button {
  background-color: #272727;
  color: #e3e3e3;
  border: none;
  padding: 8px 20px;
  border-radius: 20px;
  font-size: 13px;
  cursor: pointer;
  transition: all 0.2s ease;
}
.card .card-content .card-button:hover {
  color: #e9ff70;
  background-color: rgba(233, 255, 112, 0.05);
}

.cards {
  display: grid;
  gap: 16px;
  grid-template-columns: repeat(auto-fit, minmax(0, 1fr)); /* padrão: todos em uma linha */
  /* A partir de 992px: 2 itens por linha */
  /* A partir de 562px: 1 item por linha */
}
@media (max-width: 992px) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 562px) {
  .cards {
    grid-template-columns: 1fr;
  }
}

