}
#player-main .controls-progress-bar .controls .button.active .pause {
  display: block;
}
#player-main .controls-progress-bar .controls .button.active.loop path {
  fill: #e9ff70;
}
#player-main .controls-progress-bar .controls .button.active.random path {
  fill: #e9ff70;
}
#player-main .controls-progress-bar .controls .button:hover svg path {
  fill: #e9ff70;
}
@media (max-width: 576px) {
  #player-main .controls-progress-bar .controls .button:hover svg path {
    fill: #fff;
  }
}
#player-main .controls-progress-bar .controls .button svg {
  height: 15px;
}
#player-main .controls-progress-bar .controls .button svg.pause {
  display: none;
}
#player-main .controls-progress-bar .controls .button svg.loop {
  height: 20px;
}
#player-main .controls-progress-bar .controls .button svg.random {
  height: 20px;
}
#player-main .controls-progress-bar .controls .download-button {
  display: none;
}
#player-main .controls-progress-bar .progress-bar-container {
  width: 350px;
  background-color: #272727;
  height: 5px;
  position: relative;
  border-radius: 10px;
}
#player-main .controls-progress-bar .progress-bar-container .progress-bar {
  height: 5px;
  background: linear-gradient(to right, #708303, #e9ff70);
  width: 0;
  position: relative;
  border-radius: 10px;
}
#player-main .controls-progress-bar .progress-bar-container .progress-bar::after {
  content: "";
  position: absolute;
  width: 15px;
  height: 15px;
  background-color: #fff;
  border-radius: 50%;
  right: -5px;
  top: -4px;
}
@media (max-width: 992px) {
  #player-main .controls-progress-bar .progress-bar-container {
    width: 90%;
  }
}
#player-main .controls-progress-bar .current-time {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.5);
  margin-top: 4px;
}
#player-main .volume-control {
  display: flex;
  gap: 5px;
  align-items: center;
  /* Estilo geral do input range */
  /* Estilo do "thumb" (o botão do slider) */
  /* Estilo do thumb no Firefox */
  /* Estilo da barra quando o slider é movido */
}
#player-main .volume-control #volume-slider {
  -webkit-appearance: none; /* Remove o estilo padrão no Chrome/Safari */
  appearance: none; /* Remove o estilo padrão */
  width: 100%; /* Tamanho do slider */
  height: 5px; /* Altura da barra */
  background: linear-gradient(to right, #708303, #e9ff70);
  border-radius: 5px; /* Cantos arredondados */
  outline: none; /* Remove o contorno ao focar */
  transition: background 0.3s ease; /* Transição suave */
}
#player-main .volume-control #volume-slider::-webkit-slider-thumb {
  -webkit-appearance: none; /* Remove o estilo padrão no Chrome/Safari */
  appearance: none; /* Remove o estilo padrão */
  width: 15px; /* Largura do thumb */
  height: 15px; /* Altura do thumb */
  border-radius: 50%; /* Tornando o thumb redondo */
  background: #fff; /* Cor do thumb */
  cursor: pointer; /* Cursor de ponteiro */
}
#player-main .volume-control #volume-slider::-moz-range-thumb {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #333;
  cursor: pointer;
}
#player-main .volume-control #volume-slider:focus {
  background: #aaa; /* Altera a cor da barra quando o slider é focado */
}
#player-main .volume-control #volume-slider:active {
  background: #888; /* Altera a cor da barra quando o slider é clicado */
}
@media (max-width: 992px) {
  #player-main .volume-control {
    display: none;
  }
}

